"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

// Mock data for demonstration
const MOCK_DESIGNS = [
  {
    id: "1",
    name: "Classic Suit",
    description: "A timeless two-piece suit perfect for formal occasions. Made with premium wool blend fabric.",
    price: 599.99,
    category: "Suits",
    imageUrl: "https://images.pexels.com/photos/3755706/pexels-photo-3755706.jpeg",
  },
  {
    id: "2",
    name: "Traditional Kaftan",
    description: "Elegant traditional kaftan with intricate embroidery. Perfect for special occasions.",
    price: 299.99,
    category: "Traditional",
    imageUrl: "https://images.pexels.com/photos/5875803/pexels-photo-5875803.jpeg",
  },
  {
    id: "3",
    name: "Business Shirt",
    description: "Custom-fitted business shirt made from high-quality cotton. Available in various colors.",
    price: 89.99,
    category: "Shirts",
    imageUrl: "https://images.pexels.com/photos/297933/pexels-photo-297933.jpeg",
  },
]

const CATEGORIES = ["Suits", "Traditional", "Shirts", "Dresses", "Blazers"]

interface DesignFormData {
  name: string
  description: string
  price: string
  category: string
  imageUrl: string
}

function DesignForm({
  initialData,
  onSubmit,
  onCancel,
}: {
  initialData?: typeof MOCK_DESIGNS[0]
  onSubmit: (data: DesignFormData) => void
  onCancel: () => void
}) {
  const [formData, setFormData] = useState<DesignFormData>({
    name: initialData?.name || "",
    description: initialData?.description || "",
    price: initialData?.price.toString() || "",
    category: initialData?.category || "",
    imageUrl: initialData?.imageUrl || "",
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSubmit(formData)
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label htmlFor="name" className="block text-sm font-medium mb-1">
          Design Name
        </label>
        <Input
          id="name"
          value={formData.name}
          onChange={(e) => setFormData({ ...formData, name: e.target.value })}
          required
        />
      </div>

      <div>
        <label htmlFor="description" className="block text-sm font-medium mb-1">
          Description
        </label>
        <Textarea
          id="description"
          value={formData.description}
          onChange={(e) => setFormData({ ...formData, description: e.target.value })}
          required
        />
      </div>

      <div>
        <label htmlFor="price" className="block text-sm font-medium mb-1">
          Price ($)
        </label>
        <Input
          id="price"
          type="number"
          step="0.01"
          value={formData.price}
          onChange={(e) => setFormData({ ...formData, price: e.target.value })}
          required
        />
      </div>

      <div>
        <label htmlFor="category" className="block text-sm font-medium mb-1">
          Category
        </label>
        <Select
          value={formData.category}
          onValueChange={(value) => setFormData({ ...formData, category: value })}
        >
          <SelectTrigger>
            <SelectValue placeholder="Select category" />
          </SelectTrigger>
          <SelectContent>
            {CATEGORIES.map((category) => (
              <SelectItem key={category} value={category}>
                {category}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      <div>
        <label htmlFor="imageUrl" className="block text-sm font-medium mb-1">
          Image URL
        </label>
        <Input
          id="imageUrl"
          value={formData.imageUrl}
          onChange={(e) => setFormData({ ...formData, imageUrl: e.target.value })}
          required
        />
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
        >
          Cancel
        </Button>
        <Button
          type="submit"
          className="bg-black text-white hover:bg-gray-900"
        >
          {initialData ? "Update Design" : "Add Design"}
        </Button>
      </div>
    </form>
  )
}

export default function Designs() {
  const [designs, setDesigns] = useState(MOCK_DESIGNS)
  const [selectedDesign, setSelectedDesign] = useState<typeof MOCK_DESIGNS[0] | null>(null)
  const [showAddDialog, setShowAddDialog] = useState(false)

  const handleAddDesign = (data: DesignFormData) => {
    // In production, implement API call to add design
    const newDesign = {
      id: (designs.length + 1).toString(),
      ...data,
      price: parseFloat(data.price),
    }
    setDesigns([...designs, newDesign])
    setShowAddDialog(false)
  }

  const handleUpdateDesign = (data: DesignFormData) => {
    // In production, implement API call to update design
    if (!selectedDesign) return

    const updatedDesigns = designs.map(design =>
      design.id === selectedDesign.id
        ? { ...design, ...data, price: parseFloat(data.price) }
        : design
    )
    setDesigns(updatedDesigns)
    setSelectedDesign(null)
  }

  const handleDeleteDesign = (id: string) => {
    // In production, implement API call to delete design
    if (!confirm("Are you sure you want to delete this design?")) return
    setDesigns(designs.filter(design => design.id !== id))
  }

  return (
    <div className="space-y-8">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold font-poppins mb-2">
            Manage Designs
          </h1>
          <p className="text-gray-600">
            Add, edit, and manage your clothing designs
          </p>
        </div>
        <Dialog open={showAddDialog} onOpenChange={setShowAddDialog}>
          <DialogTrigger asChild>
            <Button className="bg-black text-white hover:bg-gray-900">
              Add New Design
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Add New Design</DialogTitle>
            </DialogHeader>
            <DesignForm
              onSubmit={handleAddDesign}
              onCancel={() => setShowAddDialog(false)}
            />
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {designs.map((design) => (
          <Card key={design.id} className="overflow-hidden">
            <div className="aspect-square relative bg-gray-100">
              <img
                src={design.imageUrl}
                alt={design.name}
                className="object-cover w-full h-full"
              />
            </div>
            <div className="p-4">
              <h3 className="font-semibold text-lg mb-1">{design.name}</h3>
              <p className="text-gray-600 text-sm mb-2">{design.category}</p>
              <p className="font-medium mb-4">${design.price.toFixed(2)}</p>
              
              <div className="flex space-x-2">
                <Dialog>
                  <DialogTrigger asChild>
                    <Button
                      variant="outline"
                      className="flex-1"
                      onClick={() => setSelectedDesign(design)}
                    >
                      Edit
                    </Button>
                  </DialogTrigger>
                  <DialogContent>
                    <DialogHeader>
                      <DialogTitle>Edit Design</DialogTitle>
                    </DialogHeader>
                    {selectedDesign && (
                      <DesignForm
                        initialData={selectedDesign}
                        onSubmit={handleUpdateDesign}
                        onCancel={() => setSelectedDesign(null)}
                      />
                    )}
                  </DialogContent>
                </Dialog>
                <Button
                  variant="outline"
                  className="flex-1"
                  onClick={() => handleDeleteDesign(design.id)}
                >
                  Delete
                </Button>
              </div>
            </div>
          </Card>
        ))}
      </div>

      {designs.length === 0 && (
        <div className="text-center py-12">
          <p className="text-gray-500">
            No designs available. Add your first design to get started.
          </p>
        </div>
      )}
    </div>
  )
}
